body {
  font-family: 'Lato', sans-serif;
  background-color: #111;
  color: #fff;
  font-weight: 300;
}

a {
  transition: all .3s linear;
}

ul {
  padding: 0;
}

li {
  list-style: none;
}

a:hover {
  text-decoration: none;
  opacity: .5;
}

strong {
  font-weight: 700;
}

.clearfix {
  clear: both;
}

h2 {
  font-size: 28px;
  text-transform: uppercase;
  font-weight: 700;
  border-bottom: 5px solid #fff;
  display: inline-block;
  padding-bottom: 5px;
  padding-right: 80px;
  margin-bottom: 30px;
}

h3 {
  text-transform: uppercase;
  color: #f00;
  text-align: center;
  font-weight: 700;
  margin: 70px 0 50px;
  font-size: 34px;
}

header {
  height: 100px;

  .logo {
    vertical-align: -60px;
  }

  .menu {
    margin-top: 33px;
    text-align: right;

    li {
      display: inline-block;
      text-transform: uppercase;
      margin: 0 10px;
      
      a {
        color: #fff;
        font-size: 20px;
        text-decoration: none;
      }
    }
  }
}

#banner {
  img {
    max-width: 100%;
    margin: auto;
    display: block;
  }
}

#streams {
  margin-top: 30px;

  .nimo {
    float: right;

    span {
      font-size: 10px;
      margin-right: 50px;
    }
  }

  .stream {
    span {
      background: #fff;
      height: 200px;
      display: block;
    }
  }
}

#updates {
  margin-top: 50px;

  .update {
    span {
      background: #fff;
      height: 200px;
      display: block;
    }
  }
}

#championships {
  .championship {
    margin-bottom: 20px;

    .logo {
      padding: 40px 10px;
      background-color: #f00;
      text-align: center;
    }
  
    .info {
      background-color: #111;
      padding: 0 80px;
      text-align: center;
  
      p {
        text-align: center;
        text-transform: uppercase;
        font-size: 15px;
  
        &.name {
          margin: 10px 0 3px;
        }
  
        &.date {
          color: #949494;
          font-size: 10px;
          margin-bottom: -20px;
        }
      }
  
      .vs {
        font-size: 30px;
        font-weight: 900;
        margin: 0 60px;
        vertical-align: -30px;
      }
    }
  
    .see-more {
      background: #111;
  
      a {
        position: absolute;
        right: 20px;
        bottom: 20px;
        color: #515151;
        text-transform: uppercase;
        font-size: 15px;
  
        span {
          font-size: 20px;
          font-weight: 700;
          vertical-align: -1px;
        }
      }
    }
  }
}

#teams {
  ul {
    text-align: center;

    li {
      a {
        img {
          width: 100%;
        }

        span {
          display: block;
          background-color: #0e0e0e;
          color: #f00;
          text-transform: none;
          text-transform: uppercase;
          font-size: 15px;
          padding: 15px;

          &:after {
            content:"";
            display: block;
            width: 40px;
            height: 1px;
            background-color: #f00;
            margin: 5px auto;
          }
        }
      }
    }
  }
}

#footer {
  margin-top: 150px;

  #sponsors {
    background-color: #f00;
    height: 150px;
  
    ul {
      text-align: center;
      padding-top: 60px;
  
      li {
        margin: 0 20px;
        display: inline-block;
      }
    }
  }

  .wrapper {
    padding: 20px 0;
    text-align: center;
    position: relative;

    .logo {
      position: absolute;
      top: 30px;
    }

    nav {
      margin-top: 20px;
    }

    nav, p {
      width: 100%;
      font-size: 10px;
    }
  }
}

.banner-page {
  min-width: 100%;
  max-width: 1920px;
  display: block;
  margin: auto;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
}

#page-photos {
  background-color: #171717;
  margin-top: 100px;
  padding: 0 30px;

  ul {
    li {
      img {
        display: block;
        margin: 10% auto;
      }
    }
  }
}

#page-about {
  background-color: #171717;
  margin-top: 100px;
  padding: 0 30px;

  .banner-content {
    max-width: 100%;
    display: block;
    padding-top: 30px;
    margin: auto;
  }

  h2 {
    margin: 30px 20px;
  }

  .text {
    font-size: 15px;
    padding: 0 50px;
    margin: 30px 0 50px;
  }

  .data {
    text-align: center;
    margin-bottom: 50px;

    p {
      display: inline-block;
      margin: 0 50px;
      text-transform: uppercase;
      position: relative;

      &:nth-child(2) {
        &:before,
        &:after {
          content: "";
          position: absolute;
          width: 1px;
          height: 80px;
          top: 20px;
          left: -50px;
          background-color: #707070;
        }

        &:after {
          right: -50px;
          left: auto;
        }
      }

      span {
        display: block;
        font-size: 60px;
        font-weight: 900;
      }
    }
  }
}

.member {
  width: 250px;
  height: 350px;
  background-color: #111;
  display: block;
  margin: 20px auto;
  position: relative;

  img {
    position: absolute;
    z-index: 0;
  }

  .video {
    display: block;
    position: absolute;
    background-color: #f00;
    padding: 14px 9px;
    border-radius: 50%;
    width: 45px;
    height: 45px;
    top: -15px;
    right: -15px;

    span {
      display: block;
      background: url('./assets/images/logos/social.png') no-repeat -112px -2px;
      width: 29px;
      height: 17px;
    }
  }

  .social {
    width: 30px;
    padding-top: 5px;
    padding-left: 5px;
    position: relative;

    li {
      margin: 10px 0;

      a {
        span {
          display: block;
          margin: auto;
          background: url('./assets/images/logos/social.png') no-repeat -2px -2px;
          width: 10px;
          height: 16px;

          &.instagram {
            background-position: -18px -2px;
            width: 17px;
            height: 17px;;
          }

          &.twitter {
            background-position: -64px -2px;
            width: 20px;
            height: 16px;;
          }
        }
      }
    }
  }

  .info {
    p {
      position: absolute;
      text-transform: uppercase;
      bottom: 50px;
      width: 100%;
      margin: 0;
      font-size: 12px;
      text-align: center;

      &.name {
        background-color: #f00;
        font-weight: 900;
        font-style: italic;
        font-size: 15px;
        padding: 10px 0;
        bottom: 0;
      }
    }
  }
}

#page-teams {
  background-color: #171717;
  margin-top: 100px;
  padding: 50px;

  h2 {
    margin-bottom: 100px;
  }

  #teams {
    h3 {
      display: none;
    }
  }
}

#page-team {
  background-color: #171717;
  padding: 50px;
  margin-top: 100px;
}

.arrows {
  text-align: center;
  width: 100%;
  max-width: 1920px;
  position: relative;

  a {
    position: absolute;
    width: 43px;
    height: 70px;
    display: block;
    background: url('./assets/images/arrow-left.png') center top no-repeat;
    top: -160px;
    left: 50%;
    margin-left: -260px;
  
    &.right {
      background-image: url('./assets/images/arrow-right.png');
      right: 50%;
      margin-right: -260px;
      left: auto;
    }
  }

  span {
    display: inline-block;
    width: 20px;
    height: 20px;
    background-color: #f00;
    border-radius: 50%;
    margin: 60px 10px 0;

    &.active {
      width: 200px;
      border-radius: 30px;
    }
  }
}